  font-weight: normal;
  font-style: normal; }

.container {
  width: 100%;
  margin: 0 auto; }

.container:after, .container:before, .row:after, .row:before {
  content: "";
  display: table;
  clear: both; }

@media (min-width: 768px) {
  .container {
    width: 740px; } }

@media (min-width: 1200px) {
  .container {
    width: 1170px; } }

@media (min-width: 1450px) {
  .container {
    width: 1360px; } }

.col-sm-12, .col-sm-6, .col-sm-4, .col-sm-3, .col-sm-2, .col-sm-1,
.col-md-12, .col-md-6, .col-md-4, .col-md-3, .col-md-2, .col-md-1,
.col-lg-12, .col-lg-6, .col-lg-4, .col-lg-3, .col-lg-2, .col-lg-1 {
  float: left;
  padding-left: 5px;
  padding-right: 5px;
  width: 100%; }

.col-sm-12 {
  width: 100%; }

.col-sm-6 {
  width: 50%; }

.col-sm-4 {
  width: 33.3333333333%; }

.col-sm-3 {
  width: 25%; }

.col-sm-2 {
  width: 16.6666666%; }

.col-sm-1 {
  width: 8.333333333%; }

@media (min-width: 768px) {
  .col-md-12 {
    width: 100%; }
  .col-md-6 {
    width: 50%; }
  .col-md-4 {
    width: 33.3333333333%; }
  .col-md-3 {
    width: 25%; }
  .col-md-2 {
    width: 16.6666666%; }
  .col-md-1 {
    width: 8.333333333%; } }

@media (min-width: 1200px) {
  .col-lg-12 {
    width: 100%; }
  .col-lg-6 {
    width: 50%; }
  .col-lg-4 {
    width: 33.3333333333%; }
  .col-lg-3 {
    width: 25%; }
  .col-lg-2 {
    width: 16.6666666%; }
  .col-lg-1 {
    width: 8.333333333%; } }

@media (min-width: 1500px) {
  .col-xl-12 {
    width: 100%; }
  .col-xl-6 {
    width: 50%; }
  .col-xl-4 {
    width: 33.3333333333%; }
  .col-xl-3 {
    width: 25%; }
  .col-xl-2 {
    width: 16.6666666%; }
  .col-xl-1 {
    width: 8.333333333%; } }

header {
  background: url("../../images/main_picture.png");
  background-size: no-repeat;
  background-size: cover;
  background-position: center;
  min-height: 900px;
  background-color: rgba(0, 0, 0, 0.5);
  font-family: 'Helvetica Neue Cyr Thin';
  padding-top: 20px; }

.contacts {
  display: flex;
  justify-content: space-around;
  list-style-type: none; }

.contacts_img {
  margin: 0 7px 11px; }

.contacts_item-phone {
  text-align: right;
  margin-right: 20px; }

.contacts_item-adress {
  margin-left: 20px; }

.contacts_item-email {
  text-align: center; }

.contacts_link {
  display: inline-block;
  color: #ffffff;
  font-size: 18px;
  font-weight: 700;
  letter-spacing: 0.9px; }

.contacts a {
  text-decoration: none; }

.menu_box {
  margin: 0 5px; }

.menu_navigations {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 110px;
  margin: 10px auto 0;
  list-style-type: none;
  box-shadow: 0 5px 4px 1px rgba(1, 1, 1, 0.15);
  background-color: #ffffff; }

.menu_logo {
  padding: 0 0 52px 52px; }

.menu_link {
  color: #000000;
  font-size: 18px;
  font-weight: 700;
  letter-spacing: 1.34px;
  font-family: 'Roboto Condensed Light'; }

.promo_production {
  margin: 136px auto 0;
  height: 33px;
  font-size: 36px;
  color: #ffffff;
  font-weight: 400;
  letter-spacing: 4.69px;
  font-family: 'Helvetica Neue Cyr Black';
  text-align: center; }

.promo_overpay {
  margin: 37px auto 0;
  height: 33px;
  color: #ffffff;
  font-family: 'Helvetica Neue Cyr Black';
  font-size: 36px;
  font-weight: 400;
  letter-spacing: 4.69px;
  text-align: center; }

.promo_sity {
  height: 22px;
  color: #ffffff;
  font-family: 'Helvetica Neue Cyr Black';
  font-size: 20px;
  font-weight: 700;
  letter-spacing: 2px;
  margin-top: 40px;
  text-align: center; }

.promo_line {
  width: 100px;
  height: 3px;
  background-color: #fedc00;
  margin: 0 auto;
  margin-top: 40px; }

.promo_img-punkt {
  padding-bottom: 6px;
  margin-right: 14px; }

.promo_operating-mode {
  height: 18px;
  color: #ffffff;
  font-family: 'Helvetica Neue Cyr Black';
  font-size: 18px;
  font-weight: 500;
  letter-spacing: 1.35px;
  margin-top: 36px;
  text-align: center; }

.promo_btn {
  display: block;
  margin: 0 auto;
  width: 320px;
  height: 60px;
  background-color: #fedc00;
  color: #000000;
  font-family: 'Helvetica Neue Cyr Bold';
  font-size: 20px;
  font-weight: 40000;
  letter-spacing: 1px;
  text-align: center;
  padding-top: 15px;
  margin-top: 100px; }

.product {
  min-height: 890px;
  background: url("../../images/productCatalog/Фон.png"); }

.product_catalog {
  padding-top: 103px;
  padding-bottom: 103px; }

.product_list {
  height: 34px;
  color: #000000;
  font-family: 'Helvetica Neue Cyr Bold';
  font-size: 36px;
  font-weight: 700;
  letter-spacing: 2.7px;
  text-align: center; }

.product_items {
  display: flex; }

.product_background-1 {
  background: url("../../images/productCatalog/photo_beton.png");
  width: 340px;
  height: 350px; }

.product_background-2 {
  background: url("../../images/productCatalog/photo_grout.png");
  width: 340px;
  height: 350px; }

.product_background-3 {
  background: url("../../images/productCatalog/photo_ring.png");
  width: 340px;
  height: 350px; }

.product_background-4 {
  background: url("../../images/productCatalog/foto_kamienie.png");
  width: 340px;
  height: 350px; }

.product_info {
  width: 340px;
  height: 160px;
  background: rgba(154, 155, 154, 0.8);
  position: absolute;
  margin-top: 190px; }

.product_block {
  margin-top: 32px;
  margin-left: 78px; }

.product_name {
  color: #000000;
  font-family: 'Helvetica Neue Cyr Bold';
  font-size: 30px;
  font-weight: 500;
  letter-spacing: 1.5px; }

.product_class {
  height: 15px;
  color: #000000;
  font-family: 'Helvetica Neue Cyr Bold';
  font-size: 20px;
  font-weight: 500;
  letter-spacing: 1px; }

.product_line {
  width: 100px;
  height: 3px;
  background-color: #fedc00;
  margin: 23px 0 21px 1px; }

.product_block-btn {
  display: flex;
  justify-content: center;
  padding: 83px 0 86px 0; }

.product_btn-info {
  height: 19px;
  color: #fedc00;
  font-family: 'Helvetica Neue Cyr Bold';
  font-size: 20px;
  font-weight: bold;
  letter-spacing: 1px;
  text-align: center;
  width: 320px;
  height: 60px;
  border: 3px solid #fedc00;
  background-color: #ffffff;
  padding: 10px; }

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }
