
.container {
	width: 100%;
	margin: 0 auto; }

.container:after, .container:before, .row:after, .row:before {
	content: "";
	display: table;
	clear: both; }

@media (min-width: 768px) {
	.container {
		width: 740px; } }

@media (min-width: 1200px) {
	.container {
		width: 1170px; } }


@media (min-width: 1450px) {
	.container {
		width: 1360px; } }


.col-sm-12, .col-sm-6, .col-sm-4, .col-sm-3, .col-sm-2, .col-sm-1,
.col-md-12, .col-md-6, .col-md-4, .col-md-3, .col-md-2, .col-md-1,
.col-lg-12, .col-lg-6, .col-lg-4, .col-lg-3, .col-lg-2, .col-lg-1 {
	float: left;
	padding-left: 5px;
	padding-right: 5px;
	width: 100%; }

.col-sm-12 {
	width: 100%; }
.col-sm-6 {
	width: 50%; }
.col-sm-4 {
	width: 33.3333333333%; }
.col-sm-3 {
	width: 25%; }
.col-sm-2 {
	width: 16.6666666%; }
.col-sm-1 {
	width: 8.333333333%; }


